<template>
  <div>
    <el-dialog
      :title="$t('api.apiEdit')"
      :visible.sync="v"
      :before-close="handleClose"
      width="520px"
      :close-on-click-modal="false"
      append-to-body
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item :label="$t('api.currentEmail')" prop="email">
          <el-input v-model="paramForm.email" disabled></el-input>
        </el-form-item>
        <el-form-item :label="$t('api.checkCode')" prop="checkCode">
          <el-row :gutter="10">
            <el-col :span="19">
              <el-input
                v-model="paramForm.checkCode"
                :placeholder="inputPh('api.checkCode')"
              ></el-input>
            </el-col>
            <el-col :span="3">
              <el-button
                v-if="!countingDown"
                type="primary"
                size="small"
                @click="sendSms"
                >{{ $t('api.sendCode') }}</el-button
              >
              <el-button v-else :disabled="true">{{
                $t('login.countDown', { countDownNum: countDownTime })
              }}</el-button>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('api.oldSecret')" prop="oldSecretKey">
          <el-input
            v-model="paramForm.oldSecretKey"
            show-password
            :placeholder="inputPh('api.oldSecret')"
          ></el-input>
        </el-form-item>
        <el-form-item :label="$t('api.newSecret')" prop="newSecretKey">
          <el-input
            v-model="paramForm.newSecretKey"
            show-password
            :placeholder="inputPh('api.newSecret')"
          ></el-input>
        </el-form-item>
        <div class="tip">
          {{ $t('api.secretTip') }}
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" @click="clickOk">{{
          $t('common.ok')
        }}</el-button>
        <el-button size="small" @click="cancel">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    recordId: {
      type: [String, Number],
      default: '',
    },
    modifyForm: {
      type: Object,
      default: () => {
        return null
      },
    },
  },
  data() {
    return {
      countDownTime: 60,
      countingDown: false,
      paramForm: {},
      defaultForm: { email: this.$store.state.user.user.email, checkCode: '' },
      paramRules: {
        email: [{ required: true, message: '请输入', trigger: 'blur' }],
        checkCode: [{ required: true, message: '请输入', trigger: 'blur' }],
        oldSecretKey: [
          { required: true, message: '请输入旧密钥' },
          { validator: this.validatePassword, trigger: 'blur' },
        ],
        newSecretKey: [
          { required: true, message: '请输入新密钥' },
          { validator: this.validatePassword, trigger: 'blur' },
        ],
      },
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  watch: {
    visible(val) {
      if (val) {
        if (this.modifyForm) {
          this.paramForm = { ...this.modifyForm }
        } else {
          this.paramForm = { ...this.defaultForm }
        }
      }
    },
  },

  mounted() {},
  methods: {
    clickOk() {
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        fetch(
          '/apiGateway/updateGwUserSecretByCheckCode',
          { ...this.paramForm, recordId: this.recordId },
          { method: 'post' }
        ).then((res) => {
          this.$refs.paramFormRef.resetFields()
          this.$message.success(`修改成功！`)
          this.v = false
          this.$emit('refresh')
        })
      })
    },
    async sendSms() {
      await fetch(
        '/user/sendMail',
        { sceneCode: 'updateApiSecretKey' },
        { method: 'get' }
      )
      this.$message.success('发送成功!')
      this.startCountDown()
    },
    startCountDown() {
      this.countingDown = true
      const countDownInterval = setInterval(() => {
        if (this.countDownTime > 1) {
          this.countDownTime--
        } else {
          clearInterval(countDownInterval)
          this.resetCountDown()
        }
      }, 1000)
    },
    resetCountDown() {
      this.countingDown = false
      this.countDownTime = 60
    },
    cancel() {
      this.resetCountDown()
      this.$refs.paramFormRef.clearValidate()
      this.v = false
    },
    handleClose() {
      this.cancel()
    },
    validatePassword(rule, value, callback) {
      if (!/^(?=.*[a-z])(?=.*[A-Z])[A-Za-z\d]{9}$/.test(value)) {
        callback(new Error('请输入9位大小写字母和数字组合'))
        return
      }
      callback()
    },
  },
}
</script>
<style lang="scss" scoped>
.tip {
  margin-top: 20px;
  line-height: 20px;
  font-size: 14px;
  color: red;
}
</style>
