<template>
  <div class="container">
    <ApiIntro />
    <div class="container-content">
      <div class="container-header">
        <div class="header-title">
          <div class="header-left">{{ $t('api.gwUserId') + detailInfo.gwUserId }}</div>
        </div>
        <div class="header-info">
          <el-row class="header-info-bottom" :gutter="20">
            <el-col v-for="obj in infoList" :key="obj.prop" :span="4">
              <div>{{ obj.label }}</div>
              <div class="info-value-container">
                <el-tooltip
                  effect="dark"
                  :content="itemInfoFormatter(obj)"
                  placement="top"
                  :disabled="!isShowTooltip"
                >
                  <div ref="infoValue" class="info-value" @mouseenter="onMouseOver">
                    {{ itemInfoFormatter(obj) }}
                  </div>
                </el-tooltip>
                <div v-if="obj.label === 'Secret_Key'" class="info-action">
                  <a class="el-icon-view cor-blue" @click="viewPlain" />
                  <a
                    v-compPriv="48"
                    class="el-icon-edit-outline cor-blue"
                    @click="editPassword"
                  />
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
    <ViewPlainDialog
      :visible.sync="plainDialogVisible"
      :record-id="recordId"
      @refresh="viewPlainSuccess"
    ></ViewPlainDialog>
    <ResetDialog
      :visible.sync="resetDialogVisible"
      :record-id="recordId"
      @refresh="qryCmpParamPage()"
    ></ResetDialog>
  </div>
</template>
<script>
import fetch from '@/request/fetch'
import ApiIntro from './componnets/ApiIntro'
import ResetDialog from './componnets/ResetDialog'
import ViewPlainDialog from './componnets/ViewPlainDialog.vue'
export default {
  components: {
    ApiIntro,
    ViewPlainDialog,
    ResetDialog,
  },
  data() {
    return {
      isShowTooltip: true,
      plainDialogVisible: false,
      resetDialogVisible: false,
      recordId: '',
      info: {},
      detailInfo: {},
    }
  },
  computed: {
    infoList() {
      return [
        {
          label: this.$t('api.gwUserName'),
          prop: 'gwUserName',
        },
        {
          label: this.$t('api.cmpUserCode'), // 修改后的label
          prop: 'cmpUserCode',
        },
        {
          label: this.$t('api.cmpUserName'), // 修改后的label
          prop: 'cmpUserName',
        },
        {
          label: this.$t('api.appKey'), // 修改后的label
          prop: 'appKey',
        },
        {
          label: this.$t('api.secretKey'), // 修改后的label
          prop: 'secretKey',
        },
        {
          label: this.$t('api.createUserCode'), // 修改后的label
          prop: 'createUserCode',
        },
        {
          label: this.$t('api.createDate'), // 修改后的label
          prop: 'createDate',
        },
        {
          label: this.$t('api.updateUserCode'), // 修改后的label
          prop: 'updateUserCode',
        },
        {
          label: this.$t('api.updateDate'), // 修改后的label
          prop: 'updateDate',
        },
      ]
    },
  },
  mounted() {
    this.qryCmpParamPage()
  },
  methods: {
    itemInfoFormatter(obj) {
      const des = obj.formatter
        ? obj.formatter(this.detailInfo[obj.prop])
        : this.detailInfo[obj.prop] || '-'
      return des
    },
    async qryCmpParamPage() {
      const param = {
        pageIndex: 1,
        pageSize: 1,
      }
      this.loading = true
      const res = await fetch('/apiGateway/gatewayAccountList', param, { method: 'post' })
      this.recordId = res.list[0].recordId ?? ''
      this.gatewayAccountDetail()
    },
    async gatewayAccountDetail(recordId) {
      const res = await fetch(
        '/apiGateway/gatewayAccountDetail',
        { recordId: this.recordId },
        {
          method: 'get',
        }
      ).finally(() => {
        this.loading = false
      })
      this.detailInfo = res
    },
    viewPlain() {
      this.plainDialogVisible = true
    },
    viewPlainSuccess(plainText) {
      this.detailInfo = { ...this.detailInfo, secretKey: plainText }
    },
    editPassword() {
      this.resetDialogVisible = true
    },
    onMouseOver(e) {
      const tag = e.target
      const clientWidth = tag.clientWidth
      const scrollWidth = tag.scrollWidth
      this.isShowTooltip = scrollWidth > clientWidth
    },
  },
}
</script>
